import React, { useState } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import AWS from 'aws-sdk';
import axios from "axios";

const YourEditorComponent = () => {
    const [content, setContent] = useState('');
    const [titleImage, setTitmeImage] = useState('');
    const [imageFile, setImageFile] = useState(null);
    const [imageFile2, setImageFile2] = useState(null);
    const [imageFile3, setImageFile3] = useState(null);
    const [imageFile4, setImageFile4] = useState(null);
    const [imageFile5, setImageFile5] = useState(null);
    const [imageFile6, setImageFile6] = useState(null);
    const [imageList, setImageList] = useState([]);
    const [formData, setFormData] = useState({
        name: '',
        category: '',
        hashtag: '',
        length: '',
        width: '',
        depth: '',
        description: '',
        steelPlate: '',
    });
    
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
          ...prevData,
          [name]: value,
        }));
      };

    
  const base64String = 'QUtJQVRWU1FDV0U3VTQ2UVJYNzY=';
  const base64String2 = 'QThKR01zdUdqUzg2MWd6ZThEejAyYzFkOGlhTndLN28xUUg5S3N0RA==';

  const id = atob(base64String);
  const pass = atob(base64String2);

  const handleImageUpload = async (file) => {
    let formData = new FormData();
    formData.append('file', file);  //  FormData 
    // AWS S3 
    const s3 = new AWS.S3({
      accessKeyId: id,
      secretAccessKey: pass,
      region: 'ap-southeast-2',  //   AWS  .
    });
    console.log(file);
    console.log(file.type)
    // S3  
    const params = {
      Bucket: 'cngtech',  //   S3   .
      Key: `images/${Date.now()}_${Math.floor(Math.random() * 1000)}.png`,
      Body: formData.get('file'),  // Blob  
      ACL: 'public-read',
        ContentType: String(file.type),
        ContentDisposition: 'inline',  //   .
    };

    const uploadResult = await s3.upload(params).promise();
    return uploadResult.Location;
  };

  const handleEditorChange = (value) => {
    setContent(value);
  };

  const handleImageChange = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile(file);
  };
    
  const handleImageChange2 = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile2(file);
  };
    
  const handleImageChange3 = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile3(file);
  };
    
  const handleImageChange4 = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile4(file);
  };
    
  const handleImageChange5 = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile5(file);
  };
    
  const handleImageChange6 = (event) => {
    const file = event.target.files[0];
    //   
    setImageFile6(file);
  };

  const handlePublish = async () => {
    //  
    const imageUrl = imageFile ? await handleImageUpload(imageFile) : null;
    const imageUrl2 = imageFile2 ? await handleImageUpload(imageFile2) : null;
    const imageUrl3 = imageFile3 ? await handleImageUpload(imageFile3) : null;
    const imageUrl4 = imageFile4 ? await handleImageUpload(imageFile4) : null;
    const imageUrl5 = imageFile5 ? await handleImageUpload(imageFile5) : null;
    const imageUrl6 = imageFile6 ? await handleImageUpload(imageFile6) : null;

      const imageList = [
        imageUrl2,
        imageUrl3,
        imageUrl4,
        imageUrl5,
        imageUrl6
      ].filter((file) => file !== null);
      
      await setImageList(imageList);
      await setTitmeImage(imageUrl);
    //      , content imageUrl .
    //    DB      .
    console.log('Content:', content);
    console.log('Image URL:', imageUrl);
    console.log('Image List: ', imageList);

    await fetchData(imageUrl, imageList);
  };
    
  const fetchData = async (imageUrl, imageList) => {
    try {

        console.log(imageList);
        const data = {
            name: formData.name,
            category: formData.category,
            hashtag: formData.hashtag,
            titleImage: imageUrl,
            length: formData.length,
            width: formData.width,
            depth: formData.depth,
            description: formData.description,
            contents:content,
            steelPlate:formData.steelPlate,
            imageList: imageList
        }
        console.log(data)
        const response = await axios.post(`https://worldwide-gabriellia-cngtech.koyeb.app/productInsert`,data);
         //const response = await axios.post(`http://localhost:3001/productInsert`, data);
        
    alert('  ');
    console.log(response);
  } catch (error) {
    console.error('Error fetching data:', error);
    alert('    ');
  }
};

  return (
    <div>
            <input type="file" onChange={handleImageChange} /> <br />
          1  <input type="file" onChange={handleImageChange2} /> <br />
          2  <input type="file" onChange={handleImageChange3} /> <br />
          3  <input type="file" onChange={handleImageChange4} /> <br />
          4  <input type="file" onChange={handleImageChange5} /> <br />
          5  <input type="file" onChange={handleImageChange6} /> <br />
          <hr />
          <br />
          <p><strong> </strong></p>
          <br />
          <div>
            <label htmlFor="name"> : </label>
            <input
              type="text"
              id="name"
                  name="name"
                  placeholder=''
              value={formData.name}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="category"> (1: 2:// 3: 4: ) : </label>
            <input
              type="text"
              id="category"
                  name="category"
                  placeholder='category'
              value={formData.category}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="hashtag"> : </label>
            <input
              type="text"
              id="hashtag"
                  name="hashtag"
                  placeholder='hashtag'
              value={formData.hashtag}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="length">Length : </label>
            <input
              type="text"
              id="length"
                  name="length"
                  placeholder='length'
              value={formData.length}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="width">Width : </label>
            <input
              type="text"
              id="width"
                  name="width"
                  placeholder='width'
              value={formData.width}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="depth">Depth : </label>
            <input
              type="text"
              id="depth"
                  name="depth"
                  placeholder='depth'
              value={formData.depth}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="description">  : </label>
            <textarea
              id="description"
                  name="description"
                  placeholder='.'
              value={formData.description}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label htmlFor="steelPlate">  : </label>
            <input
              type="text"
              id="steelPlate"
                  name="steelPlate"
                  placeholder='.'
              value={formData.steelPlate}
              onChange={handleInputChange}
            />
          </div>
        
          
      <ReactQuill
        value={content}
        onChange={handleEditorChange}
        modules={{ toolbar: [['bold', 'italic', 'underline', 'strike'], ['list', 'bullet'], ['link', 'image'], ['clean']] }}
        formats={['bold', 'italic', 'underline', 'strike', 'list', 'bullet', 'link', 'image']}
      />
      <button onClick={async() => {
        await handlePublish()
        // await fetchData();
      }
      }> </button>
    </div>
  );
};

export default YourEditorComponent;
